<template>
  <div class="songlistetails">
    <div>
      <van-nav-bar left-text="返回" fixed @click-left="goback" left-arrow />
    </div>
    <div class="songlist-top songalbum-top clearfix"  :style="{'background-image': 'url('+songlisturl+')'}">
      <div class="songlist-img-box fl">
        <img class="auto-img" :src="songlisturl" />
      </div>
      <div class="songlist-title-box fl">
        <div class="songlist-title">
          {{songlistname}}
        </div>
        <div class="title-img-box">
          <div class="title-img-box-name">{{songlistarname}}</div>
        </div>
        <div class="title-text">
          {{songlistdescription}}
        </div>
      </div>
    </div>
    <div class="song-list-box">
      <div class="song-list-top">
        <div class="song-list-left">
          <div class="song-list-img-box">
            <img class="auto-img" @click="playAllMusic" src="../assets/images/mv-play.png"/>
          </div>
          <div class="song-list-playall">播放全部<span>(共{{songListsData.length}}首)</span></div>
        </div>
        <div class="song-list-collect" >收藏</div>
      </div>
      <div class="song-list-top" v-for="(item,index) in songListsData" :key="index" @click="goPlayPage(item)">
        <div class="song-list-left">
          <div class="song-list-img-box">{{index+1}}</div>
          <div class="song-list-playall">
            <div class="song-list-content">{{item.name}}</div>
            <div class="song-list-detail">{{item.ar[0].name}} - {{item.al.name}}</div>
          </div>
        </div>
        <div @click.stop="playmusic(item)" class="playbtn"><van-icon name="play-circle-o" /></div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/SongListDetails.less";
import "../assets/less/songalbumdetails.less"
// import {setupCookie} from "../assets/js/setupCookie"
export default {
  data() {
    return {
      songListData: [],
      songListDetailsData: [],
      songListsData:[],
      songlistid:'',
      songlisturl:'',
      // songlistpicurl:'',
      songlistname:'',
      songlistarname:'',
      songlistdescription:'',
    };
  },
  created() {
    
    
    // this.getsonglistid = this.$route.query.id
    this.getsongListDetailsData(this.getsonglistid);
  },
  
  computed: {
    //获取歌单id
    getsonglistid(){
      return this.$route.query.id
    },
  },
  watch: {
    getsonglistid(val,oval){
      if (val != oval) {
        this.getsongListDetailsData(val);
      }
    },

  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
     //获取歌单详情
    getsongListDetailsData(musicid) {
      this.axios({
        method: "GET",
        url: "/album",
        params: {
          id: musicid,
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            // this.songListDetailsData = result.data.album;
            this.songlisturl = result.data.album.picUrl
            this.songlistname = result.data.album.name
            this.songlistarname = result.data.album.artists[0].name
            this.songlistdescription = result.data.album.description
            this.songListData = result.data.songs.map( v => {
              return v.id
            });
          }
          this.getmusicData();
        })
        .catch((err) => {
          console.error(err)
        });
    },
    // 获取歌曲详情
    getmusicData() {
      // // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: `/song/detail?ids=${this.songListData}`,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.songListsData = result.data.songs
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    // 播放当前歌单
    playAllMusic(){
      this.$store.commit("setplaymusicids",this.songListData)
      let musicData = {
        id:this.songListData[0],
      }
      this.$store.commit("setmusicid",musicData)
    },
     //前往详情页面
    goPlayPage(item){
       this.playmusicids = this.songListsData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
      },
    //播放不跳转
    playmusic(item){
      this.playmusicids = this.songListsData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
    },
  }
};
</script>